.form {
  padding: 0 50px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  .form-item {
    margin: 1vh;

    .form-control {
      width: 71.875vw;
    }
  }

  .message {
    height: 250px;
  }

  .submit {
    font-weight: 1000;
    margin-top: 20px;
  }
}
